<template>
	<div id="ord">
		<el-table
		    :data="tableData"
		    style="width: 100%">
		    <el-table-column type="expand">
		      <template scope="props">
		        <el-form label-position="left" inline class="demo-table-expand">
		          <el-form-item label="用户名">
		            <span>{{ props.row.userName }}</span>
		          </el-form-item>
		          <el-form-item label="店铺名称">
		            <span>{{ props.row.shopName }}</span>
		          </el-form-item>
		          <el-form-item label="收货地址">
		            <span>{{ props.row.takeAddress }}</span>
		          </el-form-item>
		          <el-form-item label="店铺 ID">
		            <span>{{ props.row.shopId }}</span>
		          </el-form-item>		        
		          <el-form-item label="店铺地址">
		            <span>{{ props.row.shopAddress }}</span>
		          </el-form-item>
		          
		        </el-form>
		      </template>
		    </el-table-column>
		    <el-table-column
		      label="订单 ID"
		      prop="orderId">
		    </el-table-column>
		    <el-table-column
		      label="总价格"
		      prop="price">
		    </el-table-column>
		    <el-table-column
		      label="订单状态"
		      prop="state">
		      <template scope="scope">		         
		         <el-tag :type="scope.row.state=='支付成功'?'success':'danger'">{{scope.row.state}}</el-tag>
		      </template>
		    </el-table-column>
		  </el-table>
		  <div class="block">		   
		     <el-pagination
		       
		       :current-page.sync="currentPage1"
		       :page-size="100"
		       layout="total, prev, pager, next"
		       :total="1000">
		     </el-pagination>
		   </div>		
	</div>
</template>

<script>
	export default {
	    data() {
	      return {
	        tableData: [],
	        currentPage1: 1,
	      }
	    },
	    created(){
	    	this.$http.get('../../static/data/orderdata.json').then(response => {
				// get body data
					console.log(response);
					console.log(response.data.tableData);
					this.tableData = response.data.tableData;
				}, response => {
				    // error callback
				    alert("加入json文件失败");
				});
	    },
	    methods: {
	      	
	      
	      	},
	      	
	    
  }
</script>
<style>
 #ord{
 	margin: 5px;
 	margin-top: 50px;
	width: 79%;
	float: right;
} 
/*列表位置宽度*/
.el-table{
	width: 99%!important;
	margin: 10px auto; 
}

/*列表内容样式设置*/
.demo-table-expand {
    font-size: 0;
}
.demo-table-expand label {
    width: 90px;
    color: #99a9bf;
}
.demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
}
</style>


